body{
    margin: 0;
    padding: 0;
}

.navbar{
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    /* background-color: #333; */
}

.navbar .wrap-nav{
    display: flex;
    /* background-color: red; */
    height: 50px;
    width: 880px;
    margin-top: 35px;
    margin-left: 50px;
    justify-content: space-between;
}

.navbar .wrap-nav .nav-menu{
    flex-direction: row;
    display: flex;
    list-style: none;
}

.navbar .wrap-nav .nav-menu a{
    text-decoration: none;
    padding: 0 20px;
    font-weight: bold;
    color: gray;
    display: flex;
    font-family: Arial, Helvetica, sans-serif;
}

.navbar .wrap-nav .nav-menu a:hover{
    font-weight: bolder;
    color: black;
}

.navbar .wrap-nav img{
    width: 130px;
    height: 33px;
    margin-top: -10px;
}

.navbar .wrap-navbar{
    display: flex;
    /* background-color: red; */
    height: 50px;
    width:350px;
    margin-top: 35px;
    margin-right: 20px;
}

.navbar .wrap-navbar .navbar-menu{
    flex-direction: row;
    display: flex;
    list-style: none;
}

.navbar .wrap-navbar .navbar-menu a{
    font-family: Arial, Helvetica, sans-serif;
    display: flex;
    justify-content: space-between;
    text-decoration: none;
    color: black;
    padding: 20px;
    margin-top: -20px;
}
.navbar .wrap-navbar .navbar-menu button{
    border-radius: 20px;
    border: 1px solid #1e90ff;
    padding-left: 40px;
    padding-right: 40px;
    height: 40px;
    margin-top: -11px;
    background-color: white;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 15px;
    cursor: pointer;
}

.cont-error{
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.cont-error .wrap-error{
    /* background-color: red; */
    width: 1000px;
    height: 500px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: row;
}

.cont-error .wrap-error .error h1{
    /* font-size: 60px; */
    font-family: Arial, Helvetica, sans-serif;
}
.cont-error .wrap-error .error p{
    font-family: Arial, Helvetica, sans-serif;
    margin-bottom: 30px;
    /* margin-top: -30px; */
}

.cont-error .wrap-error .error button{
    padding: 15px;
    padding-left: 30px;
    padding-right: 30px;
    border-radius: 30px;
    border: none;
    /* font-size: 16px; */
    font-weight: bold;
    font-family: Arial, Helvetica, sans-serif;
    background-color: #246bc9;
    color: white;
}