body{
    margin: 0;
    padding: 0;
}

/* Navbar */
.aboutnavbar{
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    /* background-color: black; */
    margin-top: -100px;
    width: 1300px;
}

.aboutnavbar .wrap-nav{
    display: flex;
    /* background-color: red; */
    width: 850px;
    margin-left: 50px;
    justify-content: space-between;
}

.aboutnavbar .wrap-nav .nav-menu{
    flex-direction: row;
    display: flex;
    list-style: none;
}

.aboutnavbar .wrap-nav .nav-menu a{
    text-decoration: none;
    padding: 0 20px;
    font-weight: bold;
    color: rgb(219, 215, 215);
    display: flex;
    font-family: Arial, Helvetica, sans-serif;
}

.aboutnavbar .wrap-nav .nav-menu a:hover{
    font-weight: bolder;
    color: white;
    cursor: pointer;
}

.aboutnavbar .wrap-nav img{
    width: 130px;
    height: 33px;
    margin-top: -10px;
}

.aboutnavbar .wrap-navbar{
    display: flex;
    width:350px;
    margin-right: 20px;
}

.aboutnavbar .wrap-navbar .navbar-menu{
    flex-direction: row;
    /* background-color: red; */
    display: flex;
    list-style: none;
}

.aboutnavbar .wrap-navbar .navbar-menu a{
    font-family: Arial, Helvetica, sans-serif;
    display: flex;
    justify-content: space-between;
    text-decoration: none;
    color: white;
    padding: 20px;
    margin-top: -20px;
}
.aboutnavbar .wrap-navbar .navbar-menu button{
    border-radius: 20px;
    border: 2px solid white;
    color: white;
    padding-left: 40px;
    padding-right: 40px;
    height: 40px;
    margin-top: -11px;
    background-color: #2161D5;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 15px;
    cursor: pointer;
}

/* mainabout */
.all-main{
    display: flex;
    justify-content: center;
    flex-direction: column;
    background-color: #2161D5;
    background-image: url(..//Assets/Group\ 1930.png);
    background-repeat: no-repeat;
    background-position: center;
    background-position-y: 1px;  
    height: 700px;
    align-items: center;
    text-align: center;
    font-family: Arial, Helvetica, sans-serif;
}
.text-about{
    color: white;
}
.text-about h1{
    margin-top: 100px;
    font-weight: bolder;
    font-size: 55px;
    color: white;
}

.text-about button{
    margin-top: 50px;
    padding: 20px;
    padding-left: 100px;
    padding-right: 100px;
    border-radius: 30px;
    border: none;
    font-weight: bold;
    font-size: 16px;
    color: #2161D5;
    background-color: white;
    cursor: pointer;
}

/* submain */
.cont-subMain{
    margin-top: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    font-family: Arial, Helvetica, sans-serif;
}

.wrap-subMain{
    /* background-color: red; */
    width: 100%;
    height: 200px;
    text-align: center;
    align-items: center;
}

.cont-subMain .wrap-subMain .wrap-bgMain{
    display: flex;
    justify-content: flex-end;
    flex-direction: row;
}

.cont-subMain .wrap-subMain .elemen{
    width: 100px;
    height: 100px;
}
.wrap-sm{
    margin-top: -100px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}
.text-subMain{
    font-weight: bold;
    font-size: 25px;
    color: #4F5665;
}

/* advisor */
.cont-advisors{
    margin-top: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    flex-direction: row;
    font-family: Arial, Helvetica, sans-serif;
    cursor: pointer;
}

.wrap-advisors{
    /* background-color: red; */
    width: 900px;
    height: 380px;
}

.wrap-advisors h1{
    color: #1E0E62;
}

.wrap-allAdv{
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    margin-top: 50px;
}

.wrap-adv .nama{
    font-size: 20px;
}
.wrap-adv .jabatan{
    margin-top: -15px;
    font-size: 15px;
}

/* POLITIC */
.cont-politic{
    margin-top: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    font-family: Arial, Helvetica, sans-serif;
}

.politic{
    text-align: center;
}

.wrap-politic{
    /* background-color: red; */
    width: 1300px;
    height: 450px;
}

.wrap-allPol{
    display: flex;
    justify-content: space-around;
    flex-direction: row;
    margin-top: 50px;
}

.wrap-ap{
    background-color: white;
    width: 400px;
    height: 200px;
    border: 2px solid lightgray;
    border-radius: 10px;
    cursor: pointer;
}
.wrap-ap:hover{
    border: 2px solid #2161D5;
}

.wrap-pol{
    display: flex;
}

.wrap-pol img{
    width: 50px;
    height: 50px;
}

.nama-politic{
    margin-top: 13px;
}
.tugas{
    margin-top: -15px;
    font-size: 15px;
}
.wrap-pol .foto{
    margin-top: 20px;
    margin-left: 20px;
    /* background-color: aqua; */
    width: 70px;
    height: 50px;
}

.text-politic{
    font-family: 'Rubik';
    margin-left: 20px;
    /* font-size: 15px; */
}


/* subscribe */
.cont-subs{
    display: flex;
    justify-content: center;
    flex-direction: row;
    margin-top: 50px;
    align-items: center;
    font-family: Arial, Helvetica, sans-serif;
}

.wrap-subs{
    background-color: #2161D5;
    width: 1100px;
    height: 250px;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 100px;
}

.wrap-subscribe{
    /* background-color: red; */
    color: white;
    width: 1000px;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    align-items: center;
}

.text-subs h1{
    font-weight: bolder;
}

.button-subs button{
    border: none;
    border-radius: 10px;
    padding: 15px;
    padding-left: 60px;
    padding-right: 60px;
    color: #2161D5;
    background-color: white;
    font-weight: bolder;
    font-size: 16px;
    box-shadow: 0px 25px 75px -20px white;
    cursor: pointer;
}

/* footer */
.cont-foot{
    display: flex;
    justify-content: center;
    flex-direction: row;
    align-items: center;
    font-family: 'Rubik', sans-serif;
}

.wrap-foot{
    background-color: whitesmoke;
    width: 100%;
    height: 410px;
    display: flex;
    justify-content: center;
    flex-direction: row;
    align-items: center;
}

.wrap-footer{
    /* background-color: red; */
    width: 1100px;
    height: 280px;
    display: flex;
    justify-content: space-between;
}

.geo-footer .geo{
    width: 140px;
    margin-bottom: 10px;
}

.geo-footer p{
    color: #333;
    margin-bottom: 30px;
}

.geo-footer .sosmed img{
    margin-left: -20px;
}

.subFooter{
    display: flex;
    flex-direction: row;
    font-size: 15px;
}

.wrap-subFooter{
    display: flex;
    justify-content: space-between;
    /* background-color: red; */
    width:500px;
}

.wrap-subFooter a{
    color: gray;
}