.aboutnavbar{
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    /* background-color: black; */
    margin-top: -100px;
    width: 1300px;
}

.aboutnavbar .wrap-nav{
    display: flex;
    /* background-color: red; */
    width: 850px;
    margin-left: 50px;
    justify-content: space-between;
}

.aboutnavbar .wrap-nav .nav-menu{
    flex-direction: row;
    display: flex;
    list-style: none;
}

.aboutnavbar .wrap-nav .nav-menu a{
    text-decoration: none;
    padding: 0 20px;
    font-weight: bold;
    color: rgb(219, 215, 215);
    display: flex;
    font-family: Arial, Helvetica, sans-serif;
}

.aboutnavbar .wrap-nav .nav-menu a:hover{
    font-weight: bolder;
    color: white;
    cursor: pointer;
}

.aboutnavbar .wrap-nav img{
    width: 130px;
    height: 33px;
    margin-top: -10px;
}

.aboutnavbar .wrap-navbar{
    display: flex;
    width:350px;
    margin-right: 20px;
}

.aboutnavbar .wrap-navbar .navbar-menu{
    flex-direction: row;
    /* background-color: red; */
    display: flex;
    list-style: none;
}

.aboutnavbar .wrap-navbar .navbar-menu a{
    font-family: Arial, Helvetica, sans-serif;
    display: flex;
    justify-content: space-between;
    text-decoration: none;
    color: white;
    padding: 20px;
    margin-top: -20px;
}
.aboutnavbar .wrap-navbar .navbar-menu button{
    border-radius: 20px;
    border: 2px solid white;
    color: white;
    padding-left: 40px;
    padding-right: 40px;
    height: 40px;
    margin-top: -11px;
    background-color: #2161D5;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 15px;
    cursor: pointer;
}