body{
    padding: 0;
    margin: 0;
}

/* Navbar */
.navbar{
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    /* background-color: #333; */
}

.navbar .wrap-nav{
    display: flex;
    /* background-color: red; */
    height: 50px;
    width: 880px;
    margin-top: 35px;
    margin-left: 50px;
    justify-content: space-between;
}

.navbar .wrap-nav .nav-menu{
    flex-direction: row;
    display: flex;
    list-style: none;
}

.navbar .wrap-nav .nav-menu a{
    text-decoration: none;
    padding: 0 20px;
    font-weight: bold;
    color: gray;
    display: flex;
    font-family: Arial, Helvetica, sans-serif;
}

.navbar .wrap-nav .nav-menu a:hover{
    font-weight: bolder;
    color: black;
}

.navbar .wrap-nav img{
    width: 130px;
    height: 33px;
    margin-top: -10px;
}

.navbar .wrap-navbar{
    display: flex;
    /* background-color: red; */
    height: 50px;
    width:350px;
    margin-top: 35px;
    margin-right: 20px;
}

.navbar .wrap-navbar .navbar-menu{
    flex-direction: row;
    display: flex;
    list-style: none;
}

.navbar .wrap-navbar .navbar-menu a{
    font-family: Arial, Helvetica, sans-serif;
    display: flex;
    justify-content: space-between;
    text-decoration: none;
    color: black;
    padding: 20px;
    margin-top: -20px;
}
.navbar .wrap-navbar .navbar-menu button{
    border-radius: 20px;
    border: 1px solid #2161D5;
    padding-left: 40px;
    padding-right: 40px;
    height: 40px;
    margin-top: -11px;
    background-color: white;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 15px;
    cursor: pointer;
}

/* main */
.cont-main{
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.cont-main .wrap-main{
    /* background-color: red; */
    width: 1300px;
    height: 500px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.cont-main .wrap-main .main h1{
    font-size: 60px;
    font-family: Arial, Helvetica, sans-serif;
}
.cont-main .wrap-main .main p{
    font-family: Arial, Helvetica, sans-serif;
    margin-bottom: 90px;
    margin-top: -30px;
}

.cont-main .wrap-main .main span{
    font-weight: bolder;
}

.cont-main .wrap-main .main button{
    padding: 20px;
    padding-left: 50px;
    padding-right: 50px;
    border-radius: 50px;
    border: none;
    font-size: 16px;
    font-weight: bolder;
    font-family: Arial, Helvetica, sans-serif;
    background-color: #2161D5;
    color: white;
    box-shadow: 60px 18px 80px #246bc980;
    cursor: pointer;
}

