body{
    padding: 0;
    margin: 0;
}

.navbar1{
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    /* background-color: #333; */
}

.navbar1 .wrap-nav{
    display: flex;
    /* background-color: red; */
    height: 50px;
    width: 880px;
    margin-top: 35px;
    margin-left: 50px;
    justify-content: space-between;
}

.navbar1 .wrap-nav .nav-menu{
    flex-direction: row;
    display: flex;
    list-style: none;
}

.navbar1 .wrap-nav .nav-menu a{
    text-decoration: none;
    padding: 0 20px;
    font-weight: bold;
    color: gray;
    display: flex;
    font-family: Arial, Helvetica, sans-serif;
}

.navbar1 .wrap-nav .nav-menu a:hover{
    font-weight: bolder;
    color: black;
}

.navbar1 .wrap-nav img{
    width: 130px;
    height: 33px;
    margin-top: -10px;
}

.navbar1 .wrap-navbar{
    display: flex;
    /* background-color: red; */
    height: 50px;
    width:350px;
    margin-top: 35px;
    margin-right: 20px;
}

.navbar1 .wrap-navbar .navbar-menu{
    flex-direction: row;
    display: flex;
    list-style: none;
}

.navbar1 .wrap-navbar .navbar-menu a{
    font-family: Arial, Helvetica, sans-serif;
    display: flex;
    justify-content: space-between;
    text-decoration: none;
    color: black;
    padding: 20px;
    margin-top: -20px;
}
.navbar1 .wrap-navbar .navbar-menu button{
    border-radius: 20px;
    border: 1px solid #1e90ff;
    padding-left: 40px;
    padding-right: 40px;
    height: 40px;
    margin-top: -11px;
    background-color: white;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 15px;
    cursor: pointer;
}