body{
    padding: 0;
    margin: 0;
    min-height: 20vh;
}

.log-cont{
    display: flex;
    /* flex-direction: row; */
    justify-content: space-between;
}

.log-cont .wrap-log{
    /* background-color: red; */
    height: 100%;
    width: 100%;
    /* margin-left: 300px; */
    display: flex;
    /* justify-content: space-between; */
    /* flex-direction: row; */
}

.log-cont .wrap-log .kotak{
    height: 100%;
    width: 500px;
    background-color: whitesmoke;
}
.log-cont .wrap-log .kotak .logo{
    margin-top: 40px;
    margin-left: 60px;
    /* width: 113px; */
}
.log-cont .wrap-log .kotak .logorg{
    margin-top: 25px;
    margin-left: 40px;
    width: 400px;
}

.log-cont .wrap-log .kotak .text-services{
    display: flex;
    justify-content: center;
    text-align: center;
    flex-direction: column;
    
}

.log-cont .wrap-log .kotak .text-services .ts{
    font-size: 22px;
    font-family: Arial, Helvetica, sans-serif;
}
.log-cont .wrap-log .kotak .text-services .ts-1{
    margin-top: -5px;
    font-size: 13px;
    color: gray;
    font-family: Arial, Helvetica, sans-serif;
}
.log-cont .wrap-log .kotak .text-services .ts-2{
    margin-right: 140px;
    margin-top: px;
    position: relative;
    color: gray;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 13px;
}

.log-cont .cont-login{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 240px;
}

.log-cont .cont-login .wrap-login{
    display: flex;
    flex-direction: column;
    width: 500px;
    height: 450px;
    background-color: white;
    justify-content: center;
}

.cont-login .wrap-login h1{
    margin-bottom: -12px;
    font-family: Arial, Helvetica, sans-serif;
}

.text-sign{
    font-family: Arial, Helvetica, sans-serif;
}


.cont-login .wrap-login .input-log .input-user{
    height: 50px;
    width: 450px;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-sizing: border-box;
    display: block;
    outline: none;
    padding: 0px 10px;
    font-size: 16px;
}

.checkbox{
    -webkit-appearance: checkbox;
    -moz-appearance: checkbox;
    appearance: checkbox;
    display: inline-block;
    width: auto;
    cursor: pointer;
}

.submit{
    margin-top: 15px;
    border-radius: 5px;
    background-color: #2161D5;
    border: none;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 15px 207px;
    text-decoration: none;
    cursor: pointer;
}

.input-log .link-log{
    text-decoration: none;
    color: white;
    font-family: Arial, Helvetica, sans-serif;
    cursor: pointer;
}

